import Branch from "./Branch";

class Tree {
  ctx: CanvasRenderingContext2D | null;
  posX: number | undefined;
  posY: number | undefined;
  branches: any[];
  depth: number;
  cntDepth: number;
  animation: number | null;
  customColors: string[];
  customColorIdx: number;

  constructor(
    ctx: CanvasRenderingContext2D | null,
    posX: number | undefined,
    posY: number | undefined,
  ) {
    this.ctx = ctx;
    this.posX = posX;
    this.posY = posY;
    this.branches = []; //   
    this.depth = 11;
    this.customColors = [
      "rgb(255, 0, 0)",
      "rgb(255, 255, 0)",
      "rgb(255, 0, 255)",
      "rgb(0, 255, 0)",
      "rgb(0, 255, 255)",
      "rgb(0, 0, 255)",
    ];

    this.cntDepth = 0; // depth    depth  
    this.animation = null; //   
    this.customColorIdx = this.random(0, 5);

    this.init();
  }

  init() {
    // depth    branches depth  
    for (let i = 0; i < this.depth; i++) {
      this.branches.push([]);
    }

    //   -90      .
    //  depth 0 .
    this.createBranch(this.posX, this.posY, -90, 0);
    this.draw();
  }

  createBranch(
    startX: number | undefined,
    startY: number | undefined,
    angle: number,
    depth: number,
  ) {
    if (depth === this.depth) return;

    // random      .
    // depth 0 ,    ,   .
    const len = depth === 0 ? this.random(10, 13) : this.random(0, 11);

    const endX =
      startX && startX + this.cos(angle) * len * (this.depth - depth);
    const endY =
      startY && startY + this.sin(angle) * len * (this.depth - depth);

    let color = "";

    if (depth < 2) {
      color = "rgb(255, 255, 255)";
    } else if (depth < 4) {
      color = this.customColors[this.customColorIdx].replaceAll("0", "239");
    } else if (depth < 6) {
      color = this.customColors[this.customColorIdx].replaceAll("0", "217");
    } else if (depth < 8) {
      color = this.customColors[this.customColorIdx].replaceAll("0", "160");
    } else if (depth < 10) {
      color = this.customColors[this.customColorIdx].replaceAll("0", "64");
    } else {
      color = this.customColors[this.customColorIdx].replaceAll("0", "32");
    }

    // depth     
    this.branches[depth].push(
      new Branch(startX, startY, endX, endY, this.depth - depth, color),
    );

    this.createBranch(endX, endY, angle - this.random(15, 23), depth + 1);
    this.createBranch(endX, endY, angle + this.random(15, 23), depth + 1);
  }

  draw() {
    //   requestAnimationFrame     .
    if (this.cntDepth === this.depth) {
      this.animation && cancelAnimationFrame(this.animation);
    }

    // depth  
    for (let i = this.cntDepth; i < this.branches.length; i++) {
      let pass = true;

      for (let j = 0; j < this.branches[i].length; j++) {
        pass = this.branches[i][j].draw(this.ctx);
      }

      if (!pass) break;
      this.cntDepth++;
    }

    const func: any = this.draw.bind(this);
    this.animation = requestAnimationFrame(func);
  }

  cos(angle: number) {
    return Math.cos(this.degToRad(angle));
  }
  sin(angle: number) {
    return Math.sin(this.degToRad(angle));
  }
  degToRad(angle: number) {
    return (angle / 180.0) * Math.PI;
  }
  random(min: number, max: number) {
    return min + Math.floor(Math.random() * (max - min + 1));
  }
}

export default Tree;
