class Branch {
  startX: number | undefined;
  startY: number | undefined;
  endX: number | undefined;
  endY: number | undefined;
  color: string;
  lineWidth: number;
  frame: number;
  cntFrame: number;
  gapX: number | undefined;
  gapY: number | undefined;
  currentX: number | undefined;
  currentY: number | undefined;

  constructor(
    startX: number | undefined,
    startY: number | undefined,
    endX: number | undefined,
    endY: number | undefined,
    lineWidth: number,
    color: string,
  ) {
    this.startX = startX;
    this.startY = startY;
    this.endX = endX;
    this.endY = endY;
    this.color = color;
    this.lineWidth = lineWidth;
    this.frame = 10; //  100    frame 
    this.cntFrame = 0; //  frame

    //   frame    
    this.gapX =
      this.endX && this.startX && (this.endX - this.startX) / this.frame;
    this.gapY =
      this.endY && this.startY && (this.endY - this.startY) / this.frame;

    //      
    this.currentX = this.startX;
    this.currentY = this.startY;
  }

  draw(ctx: CanvasRenderingContext2D) {
    //    true 
    if (this.cntFrame === this.frame) return true;

    ctx.beginPath();

    //        
    if (this.currentX && this.gapX) this.currentX += this.gapX;
    if (this.currentY && this.gapY) this.currentY += this.gapY;

    this.startX && this.startY && ctx.moveTo(this.startX, this.startY); //    
    this.currentX && this.currentY && ctx.lineTo(this.currentX, this.currentY); //    

    if (this.lineWidth < 3) {
      ctx.lineWidth = 0.5;
    } else if (this.lineWidth < 7) {
      ctx.lineWidth = this.lineWidth * 0.7;
    } else if (this.lineWidth < 10) {
      ctx.lineWidth = this.lineWidth * 0.9;
    } else {
      ctx.lineWidth = this.lineWidth;
    }

    ctx.fillStyle = this.color;
    ctx.strokeStyle = this.color;

    ctx.stroke();
    ctx.closePath();

    this.cntFrame++; //   

    //   false 
    return false;
  }
}

export default Branch;
